import { GET_STOCK_TAKE_ACTION, CREATE_JOB_SHEET } from 'apiServices/action'
import { getPORepacking } from 'apiServices'
import { getGradeRepacking } from 'apiServices/dropdown'
import { isFloat } from 'services'
import { checkIsSkuRequired, filterBySkuName, totalListItemWeightCount } from 'Page/StockEntry/GeneralServices/moduleServices'


// update sku list tobe selected
export const selectSku = async ({
    idx,
    checked,
    dispatch,
    setLocalEntryData,
    moduleName,
    moduleSummary,
    moduleEntry,
    warehouseUuid,
    areaId,
    areaDestinationId }) => {
    try {
        let moduleSummaryNew = [...moduleSummary]
        let targetRepacks = []
        moduleSummaryNew[idx].checked = checked
        dispatch({ type: GET_STOCK_TAKE_ACTION, [`${moduleName}summaryData`]: moduleSummaryNew })

        const selectedData = moduleSummaryNew?.filter(ldata => {
            if (ldata.checked) return ldata
        })
        let jsSelector = []
        if (moduleEntry) {
            moduleEntry.jobPost.list.map(data => {
                const jsList = (({ skuUuid }) => ({ skuUuid }))(data)
                if (jsList.skuUuid) {
                    jsSelector.push(jsList.skuUuid)
                }
            })
        }
        let jsD = []
        const a = selectedData.map(async (list, sI) => {
            const isInclude = jsSelector.includes(list.skuUuid)
            if (!isInclude) {
                const grade = await getGradeRepacking({ skuId: list.skuUuid })
                list['gradeList'] = grade
                const data = await getPORepacking({ skuId: list.skuUuid, warehouseId: warehouseUuid, uom: list.unitOfMeassures })
                if (data) {
                    const addPoItem = [
                        {
                            itemList: [],
                            expiryMs: null,
                            inBoundyMs: null,
                            expiryDate: null,
                            inboundDate: null,
                            skuUuid: list.skuUuid,
                            uom: list.unitOfMeassures,
                            packSize: list.packSize,
                            category: list.category,
                            initialWeight: Number.isInteger(data?.data.totalWeight) ? parseInt(data?.data.totalWeight) : parseFloat(data?.data.totalWeight),
                            targetRepacks: data?.data.targetRepacks
                        }
                    ]
                    list['poList'] = addPoItem
                    jsD.push(list)
                }

            }
        })
        const aF = await Promise.all(a)
        let newJs = []
        if (moduleEntry) {
            newJs = moduleEntry.jobPost.list.concat(jsD)
        }
        if (!moduleEntry) newJs = jsD

        const uncheckedSku = moduleSummaryNew[idx]
        if (!uncheckedSku.checked) {
            newJs = newJs.filter(data => { return data.skuUuid !== uncheckedSku.skuUuid })
        }

        const id = moduleEntry?.jobPost?.id
        const jobSheet = {
            jobPost: {
                id: id ? id : 0,
                warehouseSource: warehouseUuid,
                warehouseDestination: warehouseUuid,
                areaSource: parseInt(areaId?.value),
                areaName: areaId?.label,
                areaDestination: parseInt(areaDestinationId?.value),
                areaDestinationName: areaDestinationId?.label,
                list: newJs.sort(function (a, b) { return filterBySkuName(a, b) })
            }
        }
        if (newJs.length === 0) dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: null })
        setLocalEntryData(jobSheet)
    }
    catch (e) {
        console.log(e);
    }
}

//pre Add SKU
export const preAddSku = async ({
    idx,
    checked,
    dispatch,
    moduleName,
    moduleSummary,
    moduleEntry,
    warehouseUuid,
    areaId,
    preAddHandler }) => {
    try {
        let moduleSummaryNew = [...moduleSummary]
        moduleSummaryNew[idx].checked = checked

        const selectedData = moduleSummaryNew?.filter(ldata => {
            if (ldata.checked) return ldata
        })
        let jsSelector = []
        if (moduleEntry) {
            moduleEntry.map(data => {
                const jsList = (({ skuUuid }) => ({ skuUuid }))(data)
                if (jsList.skuUuid) {
                    jsSelector.push(jsList.skuUuid)
                }
            })
        }
        let jsD = []
        const a = selectedData.map(async (list, sI) => {
            const isInclude = jsSelector.includes(list.skuUuid)
            if (!isInclude) {
                const grade = await getGradeRepacking({ skuId: list.skuUuid })
                list['gradeList'] = grade
                const data = await getPORepacking({ skuId: list.skuUuid, warehouseId: warehouseUuid, uom: list.unitOfMeassures })
                if (data) {
                    const addPoItem = [
                        {
                            itemList: [],
                            expiryMs: null,
                            inBoundyMs: null,
                            expiryDate: null,
                            inboundDate: null,
                            skuUuid: list.skuUuid,
                            uom: list.unitOfMeassures,
                            packSize: list.packSize,
                            category: list.category,
                            initialWeight: isFloat(data?.data.totalWeight) ? isFloat(data?.data.totalWeight) : data?.data.totalWeight,
                            targetRepacks: data?.data.targetRepacks
                        }
                    ]
                    list['poList'] = addPoItem
                    list.isNewItem = true
                    jsD.push(list)
                }

            }
        })
        const aF = await Promise.all(a)
        let newJs = []
        if (moduleEntry) {
            newJs = moduleEntry.concat(jsD)
        }
        if (!moduleEntry) newJs = jsD

        const uncheckedSku = moduleSummaryNew[idx]
        if (!uncheckedSku.checked) {
            newJs = newJs.filter(data => { return data.skuUuid !== uncheckedSku.skuUuid })
        }
        preAddHandler(newJs);
    }
    catch (e) {
        console.log(e);
    }
}


export const submitValidation = ({ setActiveSKU, moduleEntry, moduleName, setConfirmation, dispatch, setIsRequired, setInvalidThreshold }) => {
    let isClear = []
    let js = { ...moduleEntry }
    let invalidThreshold = []
    const jsR = moduleEntry.jobPost.list.map(data => {
        let description = data.originalUom
        data.poList.map(po => {
            const qtyCount = totalListItemWeightCount({ moduleList: po.itemList })
            const { packSize, targetRepacks } = po
            const isTargetRepacks = targetRepacks?.length ? true : false
            po.itemList.map(item => {
                const { qty, grade, pack, note, slot } = item

                if ((qty == 0 || isNaN(qty)) && grade?.value === null) {
                    item.qtyIsEmpty = false
                    item.gradeIsEmpty = false
                    item.noteIsEmpty = false
                    item.packIsEmpty = false
                    item.packThresholdIsEmpty = false
                }
                else {
                    if (item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste') {
                        isNaN(qty) || qty == 0 || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                        !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                        grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                    }
                    else {
                        let grd = data.gradeList.filter(grd => grd.value === grade.value)
                        let ratio = grd[0].multiplier
                        let standardWeight = description?.toLowerCase() == 'gram' ? packSize / 1000 : packSize
                        let inputWeight = qty / pack
                        inputWeight = inputWeight - (standardWeight / ratio)
                        inputWeight = inputWeight / (standardWeight / ratio)
                        inputWeight = inputWeight * 100
                        if (inputWeight >= 51 || inputWeight <= -20) {
                            item.packThresholdIsEmpty = true
                            invalidThreshold.push(true)
                        }
                        else {
                            item.packThresholdIsEmpty = false
                            invalidThreshold.push(false)
                        }
                        item.packThreshold = isFloat(inputWeight)
                        isNaN(qty) || qty == 0 || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                        isNaN(pack) || pack == 0 || pack === "" || pack === null ? item.packIsEmpty = true : item.packIsEmpty = false
                        !grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                        grade?.label === 'Waste' && !note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                        if (isTargetRepacks) {
                            // !slot?.value && slot?.value?.toString() !== '0' ? item.slotIsEmpty = true : item.slotIsEmpty = false
                        }
                    }
                }

                if (!item.qtyIsEmpty) {
                    if (Number(qtyCount) > Number(po.initialWeight)) {
                        item.qtyIsEmpty = true
                    }
                    else item.qtyIsEmpty = false
                }

                if (item?.qtyIsEmpty || item?.gradeIsEmpty || item.packIsEmpty || item?.noteIsEmpty || item?.qtyToCompareIsEmpty || item?.slotIsEmpty) isClear.push(false)
                else isClear.push(true)

                return item
            })
            return po
        })
        return data
    })


    //validate there is value
    let valid = false
    jsR.map(data => {
        data.poList.map(po => {
            po.itemList.map(item => {
                let i = false
                if (item.qty) valid = true
            })
        })
    })

    js.jobPost.list = jsR
    dispatch({ type: CREATE_JOB_SHEET, [`${moduleName}EntryData`]: js })
    if (!isClear.includes(false) && valid) {
        if (typeof (setIsRequired) === 'function') setIsRequired(false)
    }
    else {
        if (typeof (setIsRequired) === 'function') setIsRequired(true)
    }

    if (!invalidThreshold.includes(true)) {
        if (typeof (setInvalidThreshold) === 'function') setInvalidThreshold(false)
    }
    else {
        if (typeof (setInvalidThreshold) === 'function') setInvalidThreshold(true)
    }

    // if (!isClear.includes(false) && valid && !invalidThreshold.includes(true)) {
    if (!isClear.includes(false) && valid) {
        if (invalidThreshold.includes(true)) return
        if (typeof (setConfirmation) === 'function') setConfirmation({ show: true, name: 'submit' })
    }
    else {
        if (typeof (setConfirmation) === 'function') setConfirmation({ show: false, name: null })
    }

    checkIsSkuRequired({ setActiveSKU, moduleEntry, moduleName })
}